import React, { Component } from "react";
import Axios from "axios";
import { addProduct } from "../utils/variables";

class AddProduct extends Component {
    constructor(props) {
        super(props);

        this.state = {
            name: "",
            description: "",
            price: 1,
            category: "",
            image: null,
            color: "",
            isAddingProduct: false
        };

        this.addNewProduct = this.addNewProduct.bind(this);
        this.handleInputChange = this.handleInputChange.bind(this);
        this.handleFileInputChange = this.handleFileInputChange.bind(this);
    }

    addNewProduct(e) {
        e.preventDefault();
        this.setState({
            isAddingProduct: true
        });
        const { name, description, price, category, image, color } = this.state;
        let formdata = new FormData();
        formdata.append("name", name);
        formdata.append("description", description);
        formdata.append("price", price);
        formdata.append("category", category);
        formdata.append("image", image);
        formdata.append("color", color);
        Axios.post(addProduct, formdata)
            .then((res) => {
                const { product } = res.data;
                this.setState({
                    isAddingProduct: false
                });
                this.props.history.push(`/product/${product._id}`);
            })
            .catch((err) => {
                this.setState({
                    isAddingProduct: false
                });
            });
    }

    handleFileInputChange(e) {
        let image = e.target.files[0];
        if (image) {
            let reader = new FileReader();
            reader.addEventListener("load", (event) => {
                this.imageRef.style.display = "block";
                this.imageRef.src = event.target.result;
            });
            reader.readAsDataURL(image);
            this.setState(
                {
                    image
                },
                () => {}
            );
        } else {
            this.imageRef.style.display = "none";
        }
    }

    handleInputChange(e) {
        const { id, value } = e.target;
        this.setState({
            [id]: value
        });
    }

    render() {
        const { name, description, category, price, color, isAddingProduct } = this.state;
        return (
            <div className='add-product'>
                <h2>Add new Product</h2>
                <div className='layout'>
                    <form onSubmit={this.addNewProduct}>
                        <div className='form-left'>
                            <div className='input-field'>
                                <input
                                    type='text'
                                    value={name}
                                    id='name'
                                    onChange={this.handleInputChange}
                                    placeholder='Product name'
                                    required
                                />
                            </div>
                            <div className='input-field'>
                                <input
                                    type='number'
                                    min={1}
                                    value={price}
                                    id='price'
                                    onChange={this.handleInputChange}
                                    placeholder='Product price'
                                    required
                                />
                            </div>
                            <div className='input-field'>
                                <input
                                    type='text'
                                    value={category}
                                    id='category'
                                    onChange={this.handleInputChange}
                                    placeholder='Product category'
                                    required
                                />
                            </div>
                            <div className='input-field'>
                                <input
                                    type='text'
                                    value={color}
                                    id='color'
                                    onChange={this.handleInputChange}
                                    placeholder='product color'
                                    required
                                />
                            </div>
                            <div className='input-field'>
                                <textarea
                                    id='description'
                                    value={description}
                                    onChange={this.handleInputChange}
                                    placeholder='Description'
                                    required></textarea>
                            </div>
                        </div>
                        <div className='file-field'>
                            <div className='temp-img'>
                                <img
                                    src=''
                                    alt=''
                                    ref={(ref) => (this.imageRef = ref)}
                                    className='preview-image'
                                />
                            </div>
                            <input
                                type='file'
                                formEncType='multipart/form-data'
                                onChange={this.handleFileInputChange}
                                required
                            />
                            <button type='submit' disabled={isAddingProduct}>
                                {isAddingProduct ? "please hold on" : "Add Product"}
                            </button>
                        </div>
                    </form>
                </div>
            </div>
        );
    }
}

export default AddProduct;
