import React, { Component } from "react";
import axios from "axios";
import { Link } from "react-router-dom";
import Spinner from "./spinner";
import { getallProducts } from "../utils/variables";

class Home extends Component {
    constructor(props) {
        super(props);

        this.state = {
            loadingProducts: true,
            products: []
        };
    }

    componentDidMount() {
        // fetch all the products
        let product = axios.get(getallProducts);
        product
            .then((res) => {
                let data = res.data;
                let { products } = data;
                console.log(products);
                this.setState({
                    products,
                    loadingProducts: false
                });
            })
            .catch((err) => {
                this.setState({
                    loadingProducts: false
                });
            });
    }

    render() {
        const { products, loadingProducts } = this.state;
        let renderProducts = products.length ? (
            products.map((product, index) => {
                const { _id, name, image, price, description } = product;
                return (
                    <Link to={`/product/${_id}`} key={index} className='product'>
                        <img src={image} alt='' />
                        <div className='bottom'>
                            <h1>{name}</h1>
                            <p>{description}</p>
                        </div>
                    </Link>
                );
            })
        ) : (
            <h1>No products</h1>
        );
        return (
            <React.Fragment>
                {loadingProducts && <Spinner />}
                <div className='home'>
                    <h2>Products</h2>
                    <div className='products'>{renderProducts}</div>
                </div>
            </React.Fragment>
        );
    }
}

export default Home;
